<template>
	<div >

	<div class="head">
        <header class="head">
           <h3>Logw</h3>
        </header>
    </div>

    <div class="nav">
      <ul class="nav bar">
        <li><router-link v-bind:to="'/'">Home</router-link></li>
        <li><router-link v-bind:to="'/about'">About</router-link></li>
        <li><router-link v-bind:to="'/contact'">Contact</router-link></li>
        <li><a href="#">seals</a></li>
      </ul>      
    </div>
	
	<div class="container">	
		<h3>We are the best web hosting site in the word </h3>
			<form class="review-form" @submit.prevent="onSubmit">
				<p>
					<label for="name">Name</label>
					<input type="text" id="name"v-model="name">
				</p>
				<p>
					<label for="review">Review</label>
					<textarea id="review" v-model="review"></textarea> 
				</p>
				<p class="submit">
					<input class="btn btn-primary" type="submit" value="Submit">
				</p>
			</form>
	</div>
		
	<br>
	    <footer>
	      @Copyright 2018
	    </footer>
	</div>
</template>

<script>
export default {
  data () {
    name: 'Contact'

    return {
        review:'null', 
        name:'null'
    }
    },
    methods: {
    	onSubmit() {
    		let productReview = {
    			name: this.name,
    			review: this.review
    		}
    		this.$emit('review-submitted')
    		this.name = null
    		this.review = null
    	}
    
  }
}
</script>

<style scoped>
  
  .nav{
    background:red;
    list-style:none;
    text-align: right;

  }

  li{
      background:black;
      display:inline-block; 
      padding:20px;
      color: #ffffff;
       
  }

  .container{
    background:green;
    text-align: centre;
  }
  .submit{
  	margin:0px 90px;
  }
  footer{
    margin:centre;
    text-align: center;
    background:red;
    padding:20px;
  }


</style>



